body {
  background-color: #c8e6c9;
  font-family: "Acme", sans-serif;
  transition: all 0.5s ease;
}

.game {
  width: 100%;
  height: 100vh;
}

.square {
  width: 50px;
  height: 50px;
  background: #a5d6a7;
  font-size: 40px;
  float: left;
  border: 1px solid #66bb6a;
  cursor: pointer;
  transition: all 1s ease;
  color: #1b5e20;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  font-size: 20px;
  text-align: center;
  color: #388e3c;
}

.status-win {
  font-size: 20px;
  text-align: center;
  color: #ee6002;
}

.info-list {
  color: #1b5e20;
  list-style-type: none;
}
.info-list li button {
  width: 150px;
}
